import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";

const CorporateCost = (props) => {
    const [searchValue, setSearchValue] = React.useState('');
    const prepearAmount = (amount, type) => {
        switch (type) {
            case 'loss':
                return (
                    <span className="red">{amount}</span>)
                break;
            case 'profit':
                return (<span className="green">{amount}</span>)
                break;
            default:
                return amount
                break;
        }
    }


    const itemHTML = (i, key) => {
        return  (   <TableRow key={key}>
            <TableCell className="padding-sides-25 red text-align-right">${i.amount}</TableCell>
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside inline align-items-baseline">{IconPack.userIcons.forTable}</span>{i.user}</TableCell>
            <TableCell className="padding-sides-10 color-white">{i.costAction}</TableCell>
            <TableCell className="padding-sides-20 color-white">{i.costDetail[0]}
                <span className="ml-10 icon-inside inline align-items-baseline">{IconPack.licenseIcons.cog} {i.costDetail[1]}</span>
            </TableCell>
            <TableCell className="padding-sides-20 color-white text-align-right">{i.boughtLeft}</TableCell>
            <TableCell className="padding-sides-15 color-grey">3 Apr, 19:20:55</TableCell>
        </TableRow>);
    }


    let tbodyData = (<>
        <TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="6">
                <b className="color-white">Today</b>
                <span className="color-grey inline">April 3</span>
            </TableCell>
        </TableRow>

        {props.costsData.map((i, key) => {
            return itemHTML(i, key);
        })}


        <TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="6">
                <b className="color-white">Yesterday</b>
                <span className="color-grey inline">April 2</span>
            </TableCell>
        </TableRow>

        {props.costsData.map((i, key) => {
            return itemHTML(i, key);
        })}
        </>);


    let searchedData = props.costsData.filter(i => {

        if (i.user.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costAction.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costDetail[0].toLowerCase().includes(searchValue.toLowerCase()) ||
            i.costDetail[1].toLowerCase().includes(searchValue.toLowerCase()) ||
            i.boughtLeft.toLowerCase().includes(searchValue.toLowerCase())
          ) {
            return i
        }

    })

    searchedData = searchedData.map((i, key) => {

        return itemHTML(i, key);
    });


    /* active */
    return (
        <div className="ÑustomStyle">
            <div className="filterPanel">

                <TextField
                    variant="filled"
                    fullWidth
                    className="search_area"
                    placeholder="Search"
                    value={searchValue}
                    onChange={(e)=>{setSearchValue(e.target.value)}}
                >


                </TextField>
                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>
                    <div>{IconPack.searchComponents.filter}</div>
                </div>
            </div>
            <Table className="configTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-25 w125 text-align-right">Amount</TableCell>
                        <TableCell className="padding-sides-15 w150">User</TableCell>
                        <TableCell className="padding-sides-10 w120">Cost action</TableCell>
                        <TableCell className="padding-sides-20 ">Cost detail</TableCell>
                        <TableCell className="padding-sides-20 w125 text-align-right">Bought left</TableCell>
                        <TableCell className="padding-sides-15 w125">Date</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {searchValue.length ? searchedData : tbodyData }
                </TableBody>
            </Table>
        </div>
    );
}

const mapStateToProps = (state) => ({
    costsData: state.corporateLicenseCosts.costs,
});


export default connect(mapStateToProps)(CorporateCost);